import * as React from "react"
import { useContext, useEffect } from "react"
import { Transition } from "framer-motion"
import { LayoutIdContext, GetLayoutId } from "./LayoutIdContext"

/**
 * @internal
 */
export interface MagicMotionContextProps {
    enabled?: boolean
    transition?: Transition
    layoutOrder: number
    isPresent: false | undefined
    getLayoutId: (args: GetLayoutId) => string | null
    isInCurrentNavigationTarget?: boolean
}

/**
 * @internal
 */
export const MagicMotionContext = React.createContext<MagicMotionContextProps>({
    enabled: false,
    getLayoutId: args => null,
    isInCurrentNavigationTarget: true,
    layoutOrder: 0,
    isPresent: undefined,
})

/**
 * @internal
 */
export function MagicMotionProvider({
    enabled,
    layoutOrder,
    isCurrentNavigationTarget,
    areMagicMotionLayersPresent,
    children,
}: {
    enabled?: boolean
    layoutOrder: number
    isCurrentNavigationTarget?: boolean
    areMagicMotionLayersPresent: false | undefined
    children: React.ReactNode
    onComplete?: () => void
}) {
    const { getLayoutId, registerScreen } = useContext(LayoutIdContext)

    useEffect(() => registerScreen(), [registerScreen])
    return (
        <MagicMotionContext.Provider
            value={{
                enabled,
                getLayoutId,
                layoutOrder,
                isInCurrentNavigationTarget: isCurrentNavigationTarget,
                isPresent: areMagicMotionLayersPresent,
            }}
        >
            {children}
        </MagicMotionContext.Provider>
    )
}

/**
 * Used to know if a code component is a child of the current screen.
 * This can be useful for resetting timers or interactions that need to trigger on every navigation.
 * @beta
 */
export function useIsInCurrentNavigationTarget() {
    // magicDependency is `true` if the MagicMotionProvider at the root of each screen is the current screen.
    const { isInCurrentNavigationTarget } = useContext(MagicMotionContext)

    return isInCurrentNavigationTarget
}
